*
{
  margin: 0;
  padding: 0;
}
body{
  background: url("pattern.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.header{
  text-align: center;
  color: rgba(13, 3, 48, 0.904);
  }
  .app{
    background: linear-gradient(203deg,#ff00c0 0%, #71715a 40%, #00daff 80%);
    width: 95%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0,0,0,0.75);
    display: flex;
    justify-content: space-evenly;
  }
  .score-section{
    display: flex;
    align-items: center;
    font-size: 30px;
    color: white;
    font-weight: bold;
  }
  .question-section{
    width: 100%;
    position: relative;
  }
  .question-count{
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: bold;
  }
  .question-count span{
    font-size: 30px;
    font-weight: bold;
  }
  .question-text{
    color: rgb(29, 29, 29);
    font-size: 25px;
    font-weight: bold;
  }
  .answer-section{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
  }
  button{
    width: 95%;
    font-size: 16px;
    color: rgb(29, 29, 29);
    background: linear-gradient(203deg,#ff00c0 0%, #71715a 40%, #00daff 80%);
    border-radius: 15px; 
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid #234668;
    cursor: pointer;
    font-weight: bold;
  }
  button:hover{
  color:rgb(39, 5, 37);
   box-shadow: 5px 5px 12px #ffffff,5px 5px 12px #520101c2;
        scale: 1.02;
  }
  .next-button{
    font-size: 15px;
    color: rgb(29, 29, 29);
    background: linear-gradient(203deg,#ff00c0 0%, #71715a 40%, #00daff 80%);
    border-radius: 15px; 
    display: flex;
    padding: 5px 0px 5px;   
    cursor: pointer;
    font-weight: bold;
    margin-top: 150px;    
    width: 20%;
  }